* {
  box-sizing: border-box;
  margin: 0;
}

.body {
  font-family: "Courier New", Courier, monospace;
  font-weight: 400;
  font-style: normal;
  min-width: 320px;
}

.container {
  max-width: 1280px;
  min-width: 320px;
  padding: 0 15px;
  margin: 0 auto;
}

.img-responsive {
  max-width: 100%;
  height: auto;
}

.visual-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a:hover {
  color: red;
}